.app-wrapper {
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translate(-50%, 0);
  min-width: 600px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 39px;
  min-width: 835px;
}

h1 {
  color: rgb(82, 82, 82);
  display: inline-block;
}

p {
  width: 500px;
  margin: 0 auto;
  color: rgb(54, 54, 54);
  margin-top: 50px;
}
